<template>
  <v-card
    color="amber"
    class="bg"
  >
    <v-img
      class="mx-auto mt-15"
      max-width="300"
      src="../assets/logo2.png"
      alt="logo"
    />
    <v-card
      width="500"
      shaped
      dark
      class="mx-auto mt-10"
      color="#00000090"
    >
      <v-card-title
        class="ms-1 text-h4"
        @click="count++, validationClick()"
      >
        <strong>Login.</strong>
      </v-card-title>

      <form>
        <v-card-text>
          <v-text-field
            v-model="userInfo.RUA"
            label="CPF"
            prepend-icon="mdi-account-circle"
            outlined
            maxlength="11"
            oninput="this.value = this.value.replace(/[^0-9]/g, '')"
          />
          <v-text-field
            v-model="userInfo.FAMILIA"
            label="Senha"
            :type="showPassword ? 'text' : 'password'"
            prepend-icon="mdi-lock"
            outlined
            hide-details="true"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showPassword = !showPassword"
          />
        </v-card-text>

        <v-divider />

        <v-card-actions class="pb-5">
          <a
            href="#"
            target=""
            small
            text
            class="
              text-decoration-none
              white--text
              text-uppercase
              text-overline
              ml-5
            "
          >
            Esqueceu a senha?
          </a>

          <v-spacer />

          <v-btn
            v-if="loginBtn"
            to="/home" 
            color="blue"
            width="100"
            class="mr-5"
            @click="logIn"
          >
            Login
          </v-btn>
        </v-card-actions>
      </form>
    </v-card>
  </v-card>
</template>

<script>
  export default {
    name: 'LoginScreen',

    data: () => ({
      showPassword: false,
      logado: '',
      loginBtn: false,
      count: 0,
      users: [],
      companies: [],
      userInfo: {
        ID: '',
        EMPRESAID: '',
        PERFIL: '',
        COR: '',
        NOMES: '',
        FAMILIA: '',
        RUA: '',
        EMAIL: '',
        CEP: '',
        NUMERO: '',
        SOBRE: '',
        IMAGE: '',
        RAZAOSOCIAL: '',
        IDEMPRESA: '',
        CNPJ: '',
        EMPRESACPF: '',
        EMPRESACEP: '',
        EMPRESANUM: '',
        CONTATO1: '',
        TIPO: '',
      },
    }),

    created () {
      this.logOut()
      this.userInfo = {}
      this.reloadOnce()
    },

    methods: {
      logOut () {
        if (window.sessionStorage) {
          if (!sessionStorage.getItem('logado')) {
            sessionStorage.removeItem('id')
            sessionStorage.removeItem('profile')
            sessionStorage.removeItem('user')
            sessionStorage.removeItem('sobrenome')
            sessionStorage.removeItem('email')
            sessionStorage.removeItem('cep')
            sessionStorage.removeItem('numero')
            sessionStorage.removeItem('sobre')
            sessionStorage.removeItem('rua')
            sessionStorage.removeItem('img')
            sessionStorage.removeItem('empid')
            sessionStorage.removeItem('razaosocial')
            sessionStorage.removeItem('empcnpj')
            sessionStorage.removeItem('empcpf')
            sessionStorage.removeItem('empcep')
            sessionStorage.removeItem('empnum')
            sessionStorage.removeItem('empcontato')
            sessionStorage.removeItem('emptipo')
          } else {
            window.sessionStorage.logado = this.logado
          }
        }
      },

      logIn () {
        const EMPRESAID = this.users.find((x) => x.RUA === this.userInfo.RUA).EMPRESAID
        window.sessionStorage.id = this.users.find((x) => x.RUA === this.userInfo.RUA).ID
        window.sessionStorage.profile = this.users.find((x) => x.RUA === this.userInfo.RUA).PERFIL
        window.sessionStorage.user = this.users.find((x) => x.RUA === this.userInfo.RUA).COR
        window.sessionStorage.sobrenome = this.users.find((x) => x.RUA === this.userInfo.RUA).NOMES
        window.sessionStorage.email = this.users.find((x) => x.RUA === this.userInfo.RUA).EMAIL
        window.sessionStorage.cep = this.users.find((x) => x.RUA === this.userInfo.RUA).CEP
        window.sessionStorage.numero = this.users.find((x) => x.RUA === this.userInfo.RUA).NUMERO
        window.sessionStorage.sobre = this.users.find((x) => x.RUA === this.userInfo.RUA).SOBRE
        window.sessionStorage.rua = this.users.find((x) => x.RUA === this.userInfo.RUA).RUA
        window.sessionStorage.img = this.users.find((x) => x.RUA === this.userInfo.RUA).IMAGE
        window.sessionStorage.empid = EMPRESAID
        window.sessionStorage.razaosocial = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).RAZAOSOCIAL
        window.sessionStorage.empcnpj = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).CNPJ
        window.sessionStorage.empcep = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).CEP
        window.sessionStorage.empnum = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).EMPRESANUM
        window.sessionStorage.empcontato = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).CONTATO1
        window.sessionStorage.emptipo = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).TIPO
        window.sessionStorage.reload = true
        this.logado = true
        window.sessionStorage.logado = this.logado
      },

      reloadOnce () {
        if (window.sessionStorage) {
          if (!sessionStorage.getItem('reload')) {
            sessionStorage.reload = true
            window.location.reload()
          } else {
            sessionStorage.removeItem('reload')
          }
        }
      },

      fetchUsers () {
        return fetch('https://sheetdb.io/api/v1/khm5ici88y1u4')
          .then((r) => r.json())
          .then((r) => {
            this.users = r
          })
      },
      fetchCompanies () {
        return fetch('https://sheetdb.io/api/v1/8nmjdq4kt9z0x')
          .then((res) => res.json())
          .then((res) => {
            this.companies = res
          })
      },
      validateCPF () {
        var sSum
        var sRemainder
        var sCPF

        sCPF = this.userInfo.RUA

        sSum = 0
        if (sCPF == "00000000000") {
          return false
        }

        for (let i=1; i<=9; i++) {
          sSum = sSum + parseInt(sCPF.substring(i-1, i)) * (11 - i)
        }
        sRemainder = (sSum * 10) % 11

        if ((sRemainder == 10) || (sRemainder == 11)) {
          sRemainder = 0
        }
        if (sRemainder != parseInt(sCPF.substring(9, 10))) {
          return false
        }

        sSum = 0
        for (let i = 1; i <= 10; i++) {
          sSum = sSum + parseInt(sCPF.substring(i-1, i)) * (12 - i)
        }
        sRemainder = (sSum * 10) % 11

        if ((sRemainder == 10) || (sRemainder == 11)) {
          sRemainder = 0
        }
        if (sRemainder != parseInt(sCPF.substring(10, 11))) {
          return false
        }

        return true
      },
      ruaId () {
        if (this.userInfo.FAMILIA && this.userInfo.FAMILIA.length > 6 && this.validateCPF()) {
          try {
            let filteredUser = this.users.find(el => el.RUA === this.userInfo.RUA)
            return filteredUser.ID
          } catch (err) {
            console.log("ERRO", err)
          }
        } else {
          return ''
        }
      },
      pswdId () {
        if (this.userInfo.FAMILIA && this.userInfo.FAMILIA.length > 6) {
          try {
            let filteredPsw = this.users.find(el => el.FAMILIA === this.userInfo.FAMILIA)
            return filteredPsw.ID
          } catch (err) {
            console.log("ERRO", err)
          }
        } else {
          return ''
        }
      },
      matchingPswd () {
        if ( this.ruaId() && this.pswdId() && this.ruaId() === this.pswdId()) {
          return true
        } else {
          return false
        }
      },
      validateLogin () {
        if (this.matchingPswd() === true) {
          this.loginBtn = true
        } else {
          sessionStorage.removeItem('logado')
        }
      },
      validationClick () {
        if (this.count >= 5) {
          this.fetchUsers()
            .then(() => {
              this.validateLogin()
            })

          this.fetchCompanies()
        } else {
          sessionStorage.removeItem('logado')
        }
      },
    },
  }
</script>

<style scoped>
  .bg {
    position: fixed;
    height: 100vh;
    width: 100vw;
    left: 0;
    top: 0;
    margin: auto;
  }
</style>